// Range

.range {
  width: 218px;
}

.range__price-range {
  display: block;
  margin-left: 18px;
  margin-bottom: 8px;
  .font-shwc(14px; 16px; 400; @basic-white);
}

.range__wrapper {
  width: 100%;
  min-height: 36px;
  padding-right: 15px;
  padding-left: 15px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 15px;
}

.range__input {
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  color: @basic-white;
}
