.sales-hits {
  width: 1200px;
  .wrapper();
  padding: 0 27px;
}

.sales-hits__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .mp-reset();
}

.sales-hits__item {
  position: relative;
  width: 267px;
  min-height: 359px;

  &:hover {
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.2);
  }
}

.sales-hits__image {
  margin-bottom: 13px;
}

.sales-hits__link {
  text-decoration-color: @basic-white;

  &:hover {
    text-decoration-color: @link-hover;
  }
}

.sales-hits__sub-title {
  .font-shwc(16px; 22px; 500; @basic-white);
  text-align: center;

  &:hover {
    color: @link-hover;
  }

  &:active {
    color: @link-active;
  }
}

.sales-hits__price {
  position: absolute;
  bottom: 110px;
  .font-shwc(45px; 46px; 700; @basic-white);
}
