// Dropdown

.dropdown-menu {
  position: absolute;
  z-index: 555;
  padding: 20px 15px;
  background-color: @basic-white;
  border: 1px solid @basic-neutral;
  border-radius: 5px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.4);
}

.dropdown-menu__input {
  width: 100%;
  min-height: 44px;
  padding: 0 14px;
  font-size: 14px;
  border: 1px solid @basic-neutral;
  border-radius: 5px;
}

.dropdown-menu__list {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 94px;
  .mp-reset();
  margin-bottom: 14px;
  border-bottom: 1px solid @basic-neutral;
}

.dropdown-menu__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 34px;
  margin-bottom: 20px;
}

.dropdown-menu__half-width {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 289px;
}

.dropdown-menu__half-width--price {
  width: 192px;
  padding-right: 20px;
}

.dropdown-menu__remove-btn {
  position: relative;
  width: 12px;
  height: 12px;
  border: none;
  background-color: transparent;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 6px;
    left: 1px;
    width: 11px;
    height: 1px;
    background-color: @basic-black;
    transform: rotate(-45deg);
  }

  &::after {
    transform: rotate(45deg);
  }
}

.dropdown-menu__good-name {
  width: 218px;
}

.dropdown-menu__good-name,
.dropdown-menu__good-weight,
.dropdown-menu__good-calc-price,
.dropdown-menu__good-price {
  .font-shwc(13px; 16px; 400; @basic-black);
}

.dropdown-menu__total {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.dropdown-menu__total-wrapper {
  display: block;
  min-width: 108px;
  margin-bottom: 16px;
  .font-shwc(15px; 18px; 700; @basic-black);
}
