// User

.user-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-width: 259px;
  .mp-reset();
}

.user-menu__item {
  position: relative;
}

.user-menu__item::before {
  content: "";
  position: absolute;
  bottom: -15px;
  width: 100%;
  height: 20px;
}

.user-menu__form {
  .mp-reset();
  display: flex;
}

.user-menu__link,
.user-menu__button {
  .font-shwc(14px, 16px, 500, @basic-white);
  .text-adt();
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 15px;

  &:hover {
    color: @basic-black;
    background-color: @basic-white;
  }
}

.user-menu__button--search {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 50%;
}

.user-menu__search {
  top: 35px;
  left: -310px;
  display: none;
  width: 341px;
  min-height: 84px;
}

.user-menu__login {
  top: 32px;
  left: -184px;
  display: none;
  width: 277px;
  min-height: 214px;
  padding: 20px 19px;
}

.user-menu__bin {
  top: 35px;
  left: -422px;
  display: none;
  width: 540px;
  min-height: 242px;
}

.user-menu__input--login,
.user-menu__input--pass {
  margin-bottom: 20px;
}

.user-menu__item:hover .user-menu__search {
  display: flex;
}

.user-menu__item:hover .user-menu__login {
  display: block;
}

.user-menu__item:hover .user-menu__bin {
  display: block;
}

.user-menu__link--login {
  min-width: 92px;
  padding-right: 16px;
  padding-left: 44px;
}

.user-menu__link--bin {
  min-width: 117px;
  padding-right: 34px;
  padding-left: 45px;
  text-align: center;
}

.user-menu__link--bin-inner {
  display: block;
  padding-right: 16px;
  padding-left: 16px;
  text-align: right;
}

.user-menu__link--active {
  font-weight: 700;
  background-color: @basic-white;
  color: @basic-black;
}

.user-menu__link--active .user-menu__bin-icon {
  fill: @basic-black;
}

.user-menu__icon {
  position: absolute;
  top: 7px;
  left: 7px;
}

.user-menu__icon--login,
.user-menu__icon--bin {
  top: 3px;
  left: 17px;
}

.user-menu__icon--bin-inner {
  top: 6px;
}

.user-menu__glass,
.user-menu__log,
.user-menu__bin-icon {
  fill: @basic-white;
}

.user-menu__button:hover .user-menu__glass,
.user-menu__link:hover .user-menu__log,
.user-menu__link:hover .user-menu__bin-icon {
  fill: @basic-black;
}
