.modal-feedback {
  position: fixed;
  z-index: 666;
  top: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.modal-feedback--show {
  display: block;
  animation-name: show;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
}
.modal-feedback__holder {
  position: fixed;
  top: 338px;
  left: calc(50% - 238px);
  width: 477px;
  min-height: 442px;
  padding-top: 16px;
  padding-right: 25px;
  padding-bottom: 30px;
  padding-left: 25px;
  background-color: #ffffff;
  border-radius: 15px;
}
.modal-feedback__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
