  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  color: #000000;
}
.modal-feedback__close-button {
  position: relative;
  width: 18px;
  height: 17px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modal-feedback__close-button::before,
.modal-feedback__close-button::after {
  content: "";
  position: absolute;
  top: 7px;
  left: -2px;
  width: 120%;
  height: 2px;
  background-color: #000000;
  transform: scale(1) rotate(45deg);
}
.modal-feedback__close-button::after {
  transform: scale(1) rotate(-45deg);
}
.modal-feedback__close-button:hover::before {
  transform: scale(0.9) rotate(45deg);
  opacity: 0.7;
}
.modal-feedback__close-button:hover::after {
  transform: scale(0.9) rotate(-45deg);
  opacity: 0.7;
}
.modal-feedback__close-button:active::before {
  transform: scale(0.6) rotate(45deg);
}
.modal-feedback__close-button:active::after {
  transform: scale(0.6) rotate(-45deg);
}
