// Copy

.copyright {
  display: flex;
  flex-direction: column;
  width: 189px;
}

.copyright__logo {
  display: block;
  width: 122px;
  height: 39px;
  margin-bottom: 5px;
}

.copyright__madeby,
.copyright__manufac {
  .font-shwc(12px; 18px; 400; @basic-white);
  text-decoration-color: rgba(255, 255, 255, 0.5);
}

.copyright__manufac {
  &:hover {
    color: @link-hover;
  }

  &:active {
    color: @link-active;
  }
}
