// Seo

.seo {
  width: 1200px;
  .wrapper();
  padding-right: 28px;
  padding-bottom: 43px;
  padding-left: 28px;
}

.seo__wrapper {
  padding-top: 29px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  background-color: palegoldenrod;
  background-image: url("../img/seo-bg-pattern.jpg");
  background-size: 47px 45px;
  border-radius: 15px;
}

.seo__title {
  .font-shwc(24px; 30px; 500; @basic-black);
}

.seo__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .mp-reset();
}

.seo__item {
  display: flex;
  flex-direction: column;
  width: 540px;
  min-height: 171px;
}

.seo__desc {
  padding-top: 22px;
  padding-left: 55px;
  .font-shwc(16px; 22px; 400; @basic-black);
  background-image: url("../img/seo-ice.svg");
  background-repeat: no-repeat;
  background-size: 49px 49px;
  background-position: 0 8px;
}

.seo__desc--leaf {
  background-image: url("../img/seo-leaf.svg");
}

.seo__desc--cow {
  background-image: url("../img/seo-cow.svg");
}

.seo__desc--term {
  background-image: url("../img/seo-term.svg");
}
