// Promo

.promotion {
  display: flex;
  justify-content: space-between;
  width: 1200px;
  .wrapper();
  padding: 0 27px;
  padding-bottom: 40px;
}

.promotion__offer {
  display: flex;
  flex-direction: column;
  width: 560px;
  min-height: 231px;
  padding: 20px;
  padding-top: 17px;
  padding-right: 20px;
  padding-bottom: 17px;
  padding-left: 20px;
  border-radius: 15px;
  background-color: #5c122b;
  background-image: url("../img/promo-bg-raspberry.png");
  background-repeat: no-repeat;
  background-size: 560px 229px;
  background-position: center;
}

.promotion__offer--choco {
  background-color: #231611;
  background-image: url("../img/promo-bg-choco.png");
}

.promotion__sub-title {
  margin-bottom: 15px;
  .font-shwc(35px; 41px; 700; @basic-white);
}

.promotion__desc {
  margin-bottom: 44px;
  .font-shwc(18px; 22px; 400; @basic-white);
}

.promotion__button {
  align-self: flex-end;
}
