// Goods
.goods {
  padding-bottom: 26px;
  border-bottom: 2px solid @basic-light;
}

.goods__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .mp-reset();
  padding-top: 25px;
  padding-bottom: 40px;
}

.goods__item {
  position: relative;
  width: 267px;
  padding-bottom: 39px;
}

.goods__item-hover {
  position: absolute;
  z-index: 2;
  top: -5px;
  left: -15px;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  width: calc(100% + 27px);
  height: calc(100% + 40px);
  padding-bottom: 23px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 8px;
}

.goods__item:hover .goods__item-hover {
  display: flex;
}

.goods__item-image {
  margin-bottom: 13px;
  border: 0 solid transparent;
  border-radius: 50%;
}

.goods__item-price {
  position: absolute;
  top: 208px;
  .font-shwc(45px; 45px; 700; @basic-white);
}

.goods__item-name {
  display: block;
  .font-shwc(16px; 22px; 500; @basic-white);
  text-align: center;
  text-decoration-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;

  &:hover {
    color: @link-hover;
  }

  &:active {
    color: @link-active;
  }
}
