// Info

.info {
  position: absolute;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  top: 70px;
  right: 27px;
  min-width: 170px;
  min-height: 38px;
}

.info__schedule {
  .font-shwc(14px, 20px, 700, @basic-white);
}

.info__phone {
  .font-shwc(22px, 26px, 700, @basic-white);

  &:hover {
    color: @link-hover;
  }

  &:active {
    color: @link-active;
  }
}
