// Blog

.blog {
  width: 560px;
  min-height: 222px;
  padding: 20px;
  border-radius: 15px;
  background-color: @basic-white;
  background-image: url("../../../img/blog-bg.jpg");
  background-repeat: no-repeat;
  background-size: 560px 220px;
  background-position: center;
}

.blog__title {
  display: block;
  margin-bottom: 7px;
  .font-shwc(16px; 22px; 500; @basic-black);
}

.blog__link {
  display: block;
  width: 281px;
  .font-shwc(24px; 30px; 700; @basic-black);

  &:hover {
    color: @link-active;
  }

  &:active {
    color: @link-hover;
  }
}
