// Socials

.socials-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 187px;
  min-height: 37px;
  .mp-reset();
  padding-top: 10px;
}

.socials-list__link {
  display: block;
  width: 37px;
  height: 37px;
  border: 3px solid @basic-neutral;
  border-radius: 50%;
}

.socials-list__icon {
  fill: @basic-light;
}

.socials-list__link:hover .socials-list__icon {
  fill: @basic-white;
}
