// Info

.info-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 310px;
  .mp-reset();
  padding-top: 10px;
  padding-left: 6px;
}

.info-list__link {
  position: relative;
  .font-shwc(13px; 18px; 400; @basic-white);
  text-decoration-color: rgba(255, 255, 255, 0.5);

  &:hover::before {
    content: "\2661";
    position: absolute;
    top: 0;
    left: -17px;
    font-size: 20px;
    color: @basic-white;
  }

  &:hover {
    color: @link-hover;
  }

  &:active {
    color: @link-active;
  }
}
