// Fillers

.fieldset-fillers {
  .mp-reset();
  border: none;
}

.fieldset-fillers__title {
  display: block;
  margin-left: 14px;
  margin-bottom: 8px;
  .font-shwc(14px; 16px; 400; @basic-white);
}

.fieldset-fillers__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  min-width: 702px;
  padding-right: 25px;
  padding-left: 25px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 15px;
}

.fieldset-fillers__wrapper {
  padding-top: 8px;
  padding-right: 7px;
  padding-left: 7px;
  padding-bottom: 8px;
}

.fieldset-fillers__label {
  position: relative;
  padding-left: 20px;
  .font-shwc(16px; 18px; 400; @basic-white);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -8px;
    width: 20px;
    height: 20px;
    border: 2px solid @basic-white;
    border-radius: 2px;
    opacity: 0.8;
  }

  &:hover::before {
    opacity: 1;
  }
}

.fieldset-fillers__input:checked ~ .fieldset-fillers__label::after {
  content: "";
  position: absolute;
  top: 1px;
  left: -3px;
  width: 18px;
  height: 10px;
  border: 3px solid @basic-white;
  border-top-style: none;
  border-right-style: none;
  opacity: 0.7;
  transform: rotate(-45deg);
}

.fieldset-fillers__input:hover ~ .fieldset-fillers__label::after {
  opacity: 1;
}

.fieldset-fillers__input:focus ~ .fieldset-fillers__label::after {
  opacity: 1;
}

.fieldset-fillers__input:disabled ~ .fieldset-fillers__label::after {
  opacity: 0.4;
}

.fieldset-fillers__input:focus ~ .fieldset-fillers__label::before {
  opacity: 1;
  outline: 2px solid #4d90fe;
}

.fieldset-fillers__input:disabled ~ .fieldset-fillers__label::before {
  opacity: 0.4;
}
