// Modal

.modal-feedback {
  position: fixed;
  z-index: 666;
  top: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-feedback--show {
  display: block;
  animation-name: show;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
}

.modal-feedback__holder {
  position: fixed;
  top: 338px;
  left: calc(50% - 238px);
  width: 477px;
  min-height: 442px;
  padding-top: 16px;
  padding-right: 25px;
  padding-bottom: 30px;
  padding-left: 25px;
  background-color: @basic-white;
  border-radius: 15px;
}

.modal-feedback__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.modal-feedback__wrapper--top {
  margin-bottom: 20px;
}

.modal-feedback__title {
  .font-shwc(24px; 28px; 500; @basic-black);
}

.modal-feedback__close-button {
  position: relative;
  width: 18px;
  height: 17px;
  background-color: transparent;
  border: none;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 7px;
    left: -2px;
    width: 120%;
    height: 2px;
    background-color: @basic-black;
    transform: scale(1) rotate(45deg);
  }

  &::after {
    transform: scale(1) rotate(-45deg);
  }

  &:hover::before {
    transform: scale(0.9) rotate(45deg);
    opacity: 0.7;
  }

  &:hover::after {
    transform: scale(0.9) rotate(-45deg);
    opacity: 0.7;
  }

  &:active::before {
    transform: scale(0.6) rotate(45deg);
  }

  &:active::after {
    transform: scale(0.6) rotate(-45deg);
  }
}

.modal-feedback__wrapper--mdl {
  flex-direction: column;
  min-height: 108px;
  margin-bottom: 20px;
}

.modal-feedback__input {
  width: 267px;
  padding-top: 8px;
  padding-right: 14px;
  padding-bottom: 8px;
  padding-left: 14px;
  border: 1px solid @basic-neutral;
  border-radius: 5px;
}

.modal-feedback__wrapper--lower {
  flex-direction: column;
}

.modal-feedback__area {
  width: 100%;
  min-height: 155px;
  margin-bottom: 25px;
  padding-top: 8px;
  padding-right: 14px;
  padding-bottom: 8px;
  padding-left: 14px;
  border: 1px solid @basic-neutral;
  border-radius: 5px;
  resize: none;
}

.modal-feedback__submit {
  margin-left: auto;
}

// Animation

@keyframes show {
  0% {
    transform: translateY(-1000px);
  }

  100% {
    transform: translateY(0);
  }
}
