// Pagination

.pagination {
  display: flex;
  // justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  width: 205px;
  .mp-reset();
  margin-left: auto;
}

.pagionation__link {
  display: block;
  width: 27px;
  padding: 6px;
  .font-shwc(16px; 18px; 500; @basic-white);
  text-decoration: none;
  text-align: center;

  &:hover {
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
  }
}

.pagionation__link--current {
  color: @basic-black;
  background-color: @basic-white;
  border-radius: 50%;

  &:hover {
    background-color: @basic-white;
  }
}

.pagionation__link--prev {
  position: relative;
  width: 25px;
  height: 25px;
  margin-right: 10px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 6px;
    top: 8px;
    width: 10px;
    height: 2px;
    background-color: @basic-white;
    transform: rotate(-45deg);
  }

  &::after {
    top: 14px;
    transform: rotate(45deg);
  }
}

.pagionation__link--next {
  position: relative;
  width: 25px;
  height: 25px;
  margin-right: 10px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 8px;
    top: 8px;
    width: 10px;
    height: 2px;
    background-color: @basic-white;
    transform: scale(-1, 1) rotate(-45deg);
  }

  &::after {
    top: 14px;
    transform: scale(-1, 1) rotate(45deg);
  }
}

.pagionation__link--prev-disabled::before,
.pagionation__link--prev-disabled::after,
.pagionation__link--next-disabled::before,
.pagionation__link--next-disabled::after {
  background-color: rgba(255, 255, 255, 0.3);
}
