// Navigation

.navigation {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 992px;
  padding-left: 10px;
}

.navigation__menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-width: 350px;
  .mp-reset();
}

.navigation__item--catalogue {
  position: relative;
}

.navigation__item--catalogue::before {
  content: "";
  position: absolute;
  bottom: -20px;
  width: 100%;
  height: 100%;
}

.navigation__item--catalogue:hover .sub-menu {
  display: block;
}

.navigation__link {
  .font-shwc(16px, 18px, 700, @basic-white);
  text-decoration-color: rgba(255, 255, 255, 0.3);

  &:hover {
    color: @basic-black;
    background-color: @basic-white;
    border-radius: 15px;
  }
}

.navigation__link--current {
  background-color: @link-current;
  border-radius: 15px;
}
