// Sub-menu

.sub-menu {
  position: absolute;
  z-index: 555;
  display: none;
  top: 35px;
  width: 145px;
  .mp-reset();
  background-color: @basic-white;
  border: 1px solid @basic-neutral;
  border-radius: 5px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.4);
}

.sub-menu__link {
  display: block;
  width: 100%;
  padding-top: 10px;
  padding-right: 20px;
  padding-bottom: 10px;
  padding-left: 20px;
  .font-shwc(14px, 16px, 400, @basic-black);
  text-decoration: none;

  &:hover {
    background-color: @link-hover;
  }

  &:active {
    background-color: @link-active;
  }
}

.sub-menu__link--current {
  background-color: @link-current;
}

.sub-menu__link--title {
  position: relative;
  font-weight: 700;

  &::before {
    content: "";
    position: absolute;
    left: 3px;
    bottom: 0;
    width: 95%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.2);
  }
}
