// Adress

.adress {
  position: absolute;
  top: 60px;
  right: 27px;
  width: 302px;
  min-height: 305px;
  .mp-reset();
  padding-top: 30px;
  padding-right: 25px;
  padding-bottom: 30px;
  padding-left: 25px;
  font-style: normal;
  background-color: @basic-white;
  border-radius: 15px;
}

.adress__order,
.adress__tel,
.adress__schedule {
  display: block;
}

.adress__title,
.adress__order,
.adress__schedule {
  .font-shwc(14px; 20px; 400; @basic-black);
}

.adress__desc,
.adress__tel {
  .font-shwc(18px; 24px; 700; @basic-black);
}

.adress__desc {
  margin-bottom: 20px;
}

.adress__tel {
  margin-bottom: 3px;
  line-height: 22px;
  text-decoration: none;
}

.adress__order {
  margin-bottom: 3px;
}

.adress__schedule {
  margin-bottom: 34px;
}
