.slider {
  position: relative;
  width: 1200px;
  padding: 0 26px;
  padding-bottom: 40px;
  .wrapper();
}

.slider__list {
  .mp-reset();
  padding-top: 312px;
}

.slider__item {
  display: none;
  flex-direction: column;
  align-items: center;
}

.slider__item--current {
  display: flex;
}

.slider__title {
  margin-bottom: 29px;
  .font-shwc(60px; 61px; 700; @basic-white);
  text-align: center;
}

.slider__button {
  width: 278px;
  font-size: 32px;
  line-height: 44px;
  text-align: center;
  text-decoration: none;
}

.slider__controls {
  position: absolute;
  bottom: 62px;
  left: 28px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 78px;
}

.slider__bullet {
  width: 20px;
  height: 20px;
  padding: 0;
  border: 2px solid @basic-light;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    background-color: rgba(255, 255, 255, 0.4);
  }
}

.slider__bullet--current {
  background-color: @basic-white;

  &:hover {
    background-color: @basic-white;
  }
}
