// Newsletter

.newsletter {
  display: flex;
  width: 560px;
  min-height: 222px;
  padding: 5px;
  background-color: @basic-white;
  border-radius: 15px;
  background-image: url("../img/newsletter-bg.jpg");
  background-repeat: no-repeat;
  background-size: 560px 220px;
  background-position: center;
}

.newsletter__overlay {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding-top: 27px;
  padding-right: 22px;
  padding-bottom: 38px;
  padding-left: 22px;
  background-color: @basic-white;
  border-radius: 15px;
}

.newsletter__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.newsletter__input {
  width: 368px;
  padding-top: 12px;
  padding-right: 14px;
  padding-bottom: 12px;
  padding-left: 14px;
  border: 1px solid @basic-neutral;
  border-radius: 5px;
}

.newsletter__button {
  width: 128px;
  padding: 10px 15px;
}
