// Breadcrumbs

.breadcrumbs__list {
  display: flex;
  justify-content: space-between;
  width: 220px;
  .mp-reset();
  padding-top: 15px;
  padding-bottom: 0;
}

.breadcrumbs__link {
  position: relative;
  .font-shwc(14px; 16px; 400; @basic-white);

  &:hover {
    color: @link-hover;
  }

  &:active {
    color: @link-active;
  }

  &::before {
    content: "\27EB";
    position: absolute;
    top: 0;
    right: -14px;
    font-size: 10px;
  }
}

.breadcrumbs__link--current {
  text-decoration: none;
  cursor: pointer;

  &:hover {
    color: @basic-white;
  }

  &:active {
    color: @basic-white;
  }

  &::before {
    content: "";
  }
}
