// Sorting

.sorting {
  .font-shwc(16px; 18px; 500; @basic-white);
}

.sorting__title {
  .font-shwc(35px; 41px; 700; @basic-white);
}

.sorting__form-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  width: 857px;
  margin-bottom: 15px;
  padding-top: 25px;
}

.sorting__form-wrapper--lower {
  padding: 0;
  align-items: flex-end;
}

.sorting__submit {
  width: 143px;
  padding-top: 6px;
  padding-right: 20px;
  padding-bottom: 6px;
  padding-left: 20px;
  .font-shwc(16px; 18px; 400; @basic-white);
  background-color: rgba(255, 255, 255, 0.3);
  border: 2px solid @basic-white;
  border-radius: 15px;
  cursor: pointer;

  &:hover {
    background-color: @basic-white;
    color: @basic-black;
  }

  &:active {
    background-color: @basic-light;
    border-color: @basic-light;
  }
}
