// Sort

.sort {
  width: 193px;
}

.sort__label {
  display: block;
  margin-left: 18px;
  margin-bottom: 8px;
  .font-shwc(14px; 16px; 400; @basic-white);
}

.sort__select {
  width: 100%;
  padding-top: 8px;
  padding-right: 12px;
  padding-bottom: 7px;
  padding-left: 12px;
  color: @basic-white;
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  border-radius: 15px;

  &:hover {
    background-color: @basic-white;
    color: @basic-black;
  }

  &:focus {
    background-color: @basic-white;
    color: @basic-black;
  }
}
