// Fieldset

.fieldset-fat {
  .mp-reset();
  border: none;
}

.fieldset-fat__title {
  display: block;
  margin-left: 18px;
  margin-bottom: 8px;
  .font-shwc(14px; 16px; 400; @basic-white);
}

.fieldset-fat__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  min-width: 414px;
  padding-right: 15px;
  padding-left: 15px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 15px;
}

.fieldset-fat__wrapper {
  padding-top: 9px;
  padding-right: 5px;
  padding-left: 5px;
  padding-bottom: 9px;
}

.fieldset-fat__label {
  position: relative;
  padding-left: 24px;
  .font-shwc(16px; 18px; 500; @basic-white);

  &::before {
    content: "";
    position: absolute;
    top: -1px;
    left: -8px;
    width: 22px;
    height: 22px;
    border: 3px solid @basic-white;
    border-radius: 50%;
    opacity: 0.8;
  }

  &:hover::before {
    opacity: 1;
  }
}

.fieldset-fat__input:checked ~ .fieldset-fat__label::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -1px;
  width: 8px;
  height: 8px;
  background-color: @basic-white;
  border-radius: 50%;
  opacity: 0.7;
}

.fieldset-fat__input:hover ~ .fieldset-fat__label::after {
  opacity: 1;
}

.fieldset-fat__input:focus ~ .fieldset-fat__label::after {
  opacity: 1;
}

.fieldset-fat__input:disabled ~ .fieldset-fat__label::after {
  opacity: 0.4;
}

.fieldset-fat__input:focus ~ .fieldset-fat__label::before {
  opacity: 1;
  outline: 2px solid #4d90fe;
}

.fieldset-fat__input:disabled ~ .fieldset-fat__label::before {
  opacity: 0.4;
}
