// Button

.button {
  min-width: 100px;
  .mp-reset();
  padding: 10px 25px;
  .font-shwc(18px; 24px; 700; @basic-white);
  text-decoration: none;
  border: none;
  background-image: linear-gradient(to bottom, @button-basic, @button-basic-darken);
  border-radius: 30px;
  box-shadow: inset 0 -1px 0 2px @button-basic-shadow;

  &:hover {
    background-image: linear-gradient(to bottom, @button-hover, @button-darken-hover);
  }

  &:active {
    background-image: linear-gradient(to bottom, @button-darken-active, @button-active);
    box-shadow: inset 0 1px 0 2px @button-basic-shadow;
  }
}
